ight) {\n    width: $width;\n    height: $height;\n}\n\n//字体大小、行高、字体\n@mixin font($size, $line-height, $family: 'Microsoft YaHei') {\n    font: #{$size}/#{$line-height} $family;\n}\n\n//字体大小，颜色\n@mixin sc($size, $color) {\n    font-size: $size;\n    color: $color;\n}\n\n@mixin boxSizing {\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box;\n}\n\n//flex 布局和 子元素 对其方式\n@mixin fj($type: space-between) {\n    display: flex;\n    justify-content: $type;\n}\n\n@mixin ellipsis1 {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n@mixin ellipsis2($num) {\n    display: -webkit-box;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    word-wrap: break-word;\n    white-space: normal !important;\n    -webkit-line-clamp: $num;\n    /*! autoprefixer: off */\n    -webkit-box-orient: vertical;\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '../common/style/mixin';\n.footer{\n    position:absolute;\n    left:0;\n    bottom:0;\n    width:100%;\n    height:50px;\n    line-height:50px;\n    background:rgba(94,68,41,.9);\n    font-size:14px;\n    text-align:center;\n    color:#fff;\n    span{\n        i{\n            margin:0 10px;\n            font-style: normal;\n        }\n        &:nth-child(1){\n            margin-right:50px;\n        }\n    }\n}\n\n\n"]}]);




